import { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import 'bootstrap/dist/css/bootstrap.min.css';
import './ConfigPage.css';

type Env = {
  APP_NAME: string;
  APP_VERSION: string;
  APP_COPYRIGHT: string;
  APP_AUTHOR: string;
  APP_LICENSE: string;
  APP_DESCRIPTION: string;
};

export function ConfigPage() {
  const [name, setName] = useState<Env | null>(null);
  const [validated, setValidated] = useState(false);
  const [configData, setConfigData] = useState(false);

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
    const strInsert =
      "Insert into config (key, value) values ('DeviceName', '" +
      form.DeviceName.value +
      "')";
    window.electron.dbQuery(strInsert).then(() => {
      console.log('Configuration saved successfully');
      window.electron.dbQuery('Select * from config').then((res) => {
        setConfigData(res);
      });
    });
  };
  useEffect(() => {
    window.electron
      .getEnv()
      .then((res) => {
        setName(res);
        return res;
      })
      .catch((err) => {
        console.error('Error fetching environment variables:', err);
      });
  }, []);

  return (
    <div className="BdeConfig">
      <h1>Konfiguration</h1>
      <p>
        Dieses Gert ist noch nicht konfiguriert. Legen Sie einen Namen fest und
        die IPV4 Adresse des BDE Servers.
      </p>
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <fieldset>
          <Form.Group className="mb-3">
            <Form.Label htmlFor="DeviceName">Gertename</Form.Label>
            <Form.Control id="DeviceName" placeholder="BDE00x" required />
            <Form.Control.Feedback type="invalid">
              Der Gertename ist erforderlich.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label htmlFor="disabledSelect">IPV4 Adresse</Form.Label>
            <Form.Control
              id="disabledSelect"
              placeholder="10.10.10.0"
              required
            />
            <Form.Control.Feedback type="invalid">
              Die IPV4 Adresse des Servers ist erforderlich.
            </Form.Control.Feedback>
          </Form.Group>

          <Button type="submit" className="btn btn-warning">
            Speichern und Verbinden
          </Button>
        </fieldset>
      </Form>
      <p> {JSON.stringify(configData)}</p>
      <p>
        {name?.APP_NAME} {name?.APP_VERSION}
      </p>
    </div>
  );
}
export default ConfigPage;
